export class Item {
  id = "";
  itemicon = "";
  name = "";
  metadata = new Metadata();
  rank = "";
  description = "";
};

export class Metadata {
  splitList = [] as string[];
  isAvailableForUse = false; // 使用可能
  isExclusiveOnDeath = false; // 死亡時専用
  isTradeNotAllowed = false; // トレード不可
  isNotUsableDuringCombat = false; // 戦闘中使用不可
  isArenaNotAvailable = false; // アリーナ使用不可
  isNotShareableWarehouse = false; // 共有倉庫不可
  isQuestItem = false; // クエストアイテム
  isWorldMapNotAvailable = false; // ワールドマップ使用不可
}

export function createItemRow(id: string, itemicon: string, name: string, metadata: string, rank: string, description: string) {
  const item = new Item();
  item.id = id;
  item.itemicon = itemicon;
  item.name = name;
  item.rank = rank;
  item.description = description;

  const splitList = Number(metadata).toString(2).split("").reverse();
  item.metadata.splitList = splitList;
  
  if (splitList.length >= 1) item.metadata.isAvailableForUse = splitList[0] == "1"; // 使用可能
  if (splitList.length >= 3) item.metadata.isExclusiveOnDeath = splitList[2] == "1"; // 死亡時専用
  if (splitList.length >= 4) item.metadata.isTradeNotAllowed = splitList[3] == "1"; // トレード不可
  if (splitList.length >= 11) item.metadata.isNotUsableDuringCombat = splitList[10] == "1"; // 戦闘中使用不可
  if (splitList.length >= 14) item.metadata.isArenaNotAvailable = splitList[13] == "1"; // アリーナ使用不可
  if (splitList.length >= 31) item.metadata.isNotShareableWarehouse = splitList[30] == "1"; // 共有倉庫不可
  if (splitList.length >= 32) item.metadata.isQuestItem = splitList[31] == "1"; // クエストアイテム
  if (splitList.length >= 33) item.metadata.isWorldMapNotAvailable = splitList[32] == "1"; // ワールドマップ使用不可

  return item;
}
