import { LoadingButton } from '@mui/lab';
import { AppBar, Button, Container, CssBaseline, Link, Paper, Stack, TextField, ThemeProvider, Toolbar, Typography, createTheme } from '@mui/material';
import Grid from '@mui/material/Grid';
import React from 'react';
import ItemTable from './ItemTable';
import { Item, createItemRow } from './item';

function Copyright() {
  return (
    <>
      <Typography variant="body2" color="text.secondary" align="center">
        {`Copyright © 2020 - ${new Date().getFullYear()} MoonRabbit Corporation. All Rights Reserved.`}
      </Typography>
      <Typography variant="body2" color="text.secondary" align="center">
        {`Copyright © 2020 - ${new Date().getFullYear()} X-LEGEND Entertainment Corp. All Rights Reserved.`}
      </Typography>
    </>
  );
}

function App() {
  const [keyword, setKeyword] = React.useState('星界');
  const [itemRows, setItemRows] = React.useState<Item[]>([]);

  // TODO remove, this demo shouldn't need to reset the theme.
  const defaultTheme = createTheme();

  // const didLogRef = React.useRef(false);
  // React.useEffect(() => {
  //   // In this case, whether we are mounting or remounting,
  //   // we use a ref so that we only log an impression once.
  //   if (didLogRef.current == false) {
  //     didLogRef.current = true;

  //     (async () => {
  //     })();
  //   }
  // }, []);

  const [loading, setLoading] = React.useState(false);
  const handleClick = () => {
    setLoading(true);

    const worker = new Worker(new URL('./worker.js', import.meta.url), { type: 'module' });

    // Worker から処理結果を受信したときの処理
    worker.addEventListener('message', function (e) {
      const rows = e.data.map((row: string[]) => createItemRow(row[0], row[1], row[2], row[3], row[4], row[5]));
      setItemRows(rows);
      setLoading(false);
    }, false);

    // Worker からエラーを受信したときの処理
    worker.addEventListener('error', function (e) {
      console.log('error: ', e.message);
      setLoading(false);
    }, false);

    worker.postMessage({ sql: `SELECT T1.id, T1.itemicon, T1.name, T1.metadata, T1.rank, T1.description FROM item AS T1 WHERE T1.name LIKE '%${keyword}%' ORDER BY T1.name` });
    console.log('Message posted to worker');
  }

  return (
    <ThemeProvider theme={defaultTheme}>
      <CssBaseline />
      <AppBar position="absolute" color="default" elevation={0} sx={{ position: 'relative', borderBottom: (t) => `1px solid ${t.palette.divider}`, }}>
        <Toolbar>
          <Typography variant="h6" color="inherit" noWrap>星界神話DB</Typography>
        </Toolbar>
      </AppBar>
      <Container component="main" maxWidth="md" sx={{ mb: 4 }}>
        <Paper variant="outlined" sx={{ my: { xs: 3, md: 6 }, p: { xs: 2, md: 3 } }}>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={9}>
              <TextField label="キーワード" variant="outlined" size="small" fullWidth value={keyword} onChange={(event: React.ChangeEvent<HTMLInputElement>) => {setKeyword(event.target.value);}} />
            </Grid>
            <Grid item xs={12} sm={3}>
              <LoadingButton variant="contained" disableElevation size="large" loading={loading} onClick={handleClick} fullWidth>検索</LoadingButton>
            </Grid>
            <Grid item xs={12}>
              <ItemTable rows={itemRows} />
            </Grid>
          </Grid>
        </Paper>
        <Copyright />
      </Container>
    </ThemeProvider>
  );
}

export default App;
