import * as React from 'react';
import Button from '@mui/material/Button';
import Avatar from '@mui/material/Avatar';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import PersonIcon from '@mui/icons-material/Person';
import AddIcon from '@mui/icons-material/Add';
import Typography from '@mui/material/Typography';
import { blue } from '@mui/material/colors';
import { Box, DialogContent, Grid } from '@mui/material';
import { Item } from './item';
import ItemiconComponent from './ItemiconComponent';

export default function ItemDetailDialog(props: { open: boolean; onClose: () => void; item: Item; }) {
  const { onClose, open, item } = props;

  // React.useEffect(() => {

  //   (async () => {
  //   })();
  // }, [item]);

  return (
    <Dialog onClose={() => {onClose();}} open={open} fullWidth>
      <DialogContent>
        <Box sx={{ border: 1, p: 1 }}>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={2}><ItemiconComponent item={item} /></Grid>
            <Grid item xs={12} sm={10}>{item.name}</Grid>
            
          </Grid>
        </Box>
        <Box sx={{ border: 1, p: 1, borderTop: 0 }}>
          <Grid container spacing={3}>
            {/* {item.metadata.isAvailableForUse ? <Grid item>使用可能</Grid> : null} */}
            {item.metadata.isExclusiveOnDeath ? <Grid item>死亡時専用</Grid> : null}
            {item.metadata.isTradeNotAllowed ? <Grid item>トレード不可</Grid> : null}
            {item.metadata.isNotUsableDuringCombat ? <Grid item>戦闘中使用不可</Grid> : null}
            {item.metadata.isArenaNotAvailable ? <Grid item>アリーナ使用不可</Grid> : null}
            {item.metadata.isNotShareableWarehouse ? <Grid item>共有倉庫不可</Grid> : null}
            {item.metadata.isQuestItem ? <Grid item>クエストアイテム</Grid> : null}
            {item.metadata.isWorldMapNotAvailable ? <Grid item>ワールドマップ使用不可</Grid> : null}
            {/* <Grid item xs={12} sm={12}>{item.metadata.splitList}</Grid> */}
          </Grid>
        </Box>
        <Box sx={{ border: 1, p: 1, borderTop: 0 }}>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={12} style={{ whiteSpace: "pre-wrap" }}>{item.description}</Grid>
          </Grid>
        </Box>
      </DialogContent>
    </Dialog>
  );
}
