import * as React from 'react';
import TableCell from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';
import { TableVirtuoso } from 'react-virtuoso';
import { Button } from '@mui/material';
import ItemDetailDialog from './ItemDetailDialog';
import { Item } from './item';
import ItemiconComponent from './ItemiconComponent';

export default function ItemTable(prop: { rows: Item[]; }) {
  const { rows } = prop;

  const [open, setOpen] = React.useState(false);
  const [selectedItem, setSelectedItem] = React.useState<Item>(new Item());
  
  return (
    <>
      <TableVirtuoso
        useWindowScroll
        data={rows}
        fixedHeaderContent={() => (
          <TableRow>
            <TableCell style={{ background: 'white' }}>アイコン</TableCell>
            <TableCell style={{ width: 220, background: 'white' }}>名前</TableCell>
            <TableCell style={{ background: 'white' }}>説明</TableCell>
            <TableCell />
          </TableRow>
        )}
        itemContent={(index, item) => (
          <>
            <TableCell style={{ background: 'white' }}><ItemiconComponent item={item} /></TableCell>
            <TableCell style={{ width: 220, background: 'white' }}>{item.name}</TableCell>
            <TableCell style={{ background: 'white' }}>{item.description}</TableCell>
            <TableCell style={{ background: 'white' }}><Button variant="text" onClick={() => {setSelectedItem(item); setOpen(true);}}>詳細</Button></TableCell>
          </>
        )}
      />
      <ItemDetailDialog open={open} onClose={() => {setOpen(false);}} item={selectedItem} />
    </>
  );
}