import { Item } from './item';

export default function ItemiconComponent(props: { item: Item; }) {
  const { item } = props;

  return (
    <div style={{ position: "relative" }}>
      <img src={`${process.env.REACT_APP_BASE_URL}/itemicon/${item.itemicon}.png`} loading="lazy" style={{ borderRadius: "5px" }} />
      {item.rank == "" ? <img src={`${process.env.REACT_APP_BASE_URL}/rank/white.png`} loading="lazy" style={{ position: "absolute", left: 0, top: 0 }} /> : null}
      {item.rank == "2" ? <img src={`${process.env.REACT_APP_BASE_URL}/rank/green.png`} loading="lazy" style={{ position: "absolute", left: 0, top: 0 }} /> : null}
      {item.rank == "3" ? <img src={`${process.env.REACT_APP_BASE_URL}/rank/blue.png`} loading="lazy" style={{ position: "absolute", left: 0, top: 0 }} /> : null}
      {item.rank == "4" ? <img src={`${process.env.REACT_APP_BASE_URL}/rank/orange.png`} loading="lazy" style={{ position: "absolute", left: 0, top: 0 }} /> : null}
      {item.rank == "5" ? <img src={`${process.env.REACT_APP_BASE_URL}/rank/purple.png`} loading="lazy" style={{ position: "absolute", left: 0, top: 0 }} /> : null}
      {item.rank == "6" ? <img src={`${process.env.REACT_APP_BASE_URL}/rank/gold.png`} loading="lazy" style={{ position: "absolute", left: 0, top: 0 }} /> : null}
      {item.rank == "7" ? <img src={`${process.env.REACT_APP_BASE_URL}/rank/sakura.png`} loading="lazy" style={{ position: "absolute", left: 0, top: 0 }} /> : null}
    </div>
  );
}
